@import url(https://fonts.googleapis.com/css2?family=Oleo+Script:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
.blog__page__header {
    text-align: center;
    letter-spacing: -1px;
    font-size: 50px;
}
.blogs {
    font-family: 'Oleo Script', cursive;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 40px 0;
}
.blog {
    max-width: 300px;
    margin: 16px 15px;
    background: white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.3);
    text-decoration: none;
    color:black;
    transition: all .3s ease-in-out;
}

@media screen and (max-width: 690px) {
    .blog {
      max-width: 240px;
    }
  }

@media screen and (max-width: 540px) {
   .blog {
       max-width: 280px;
   }
  }

.blog:hover {
    -webkit-transform: translateY(-10px) scale(1.1);
            transform: translateY(-10px) scale(1.1);
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.8);
    background:  rgb(31, 31, 31);
    color: gray;
}
.blog > div {
    margin-top: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 30px;
}
.blog > img {
    width: 100%;
}
.sourceName > span {
    display: inline;
    background: rgba(38, 0, 128, 0.226);
    font-size: 11px;
    padding: 5px 6px;
    color: rgba(0, 11, 43, 0.534);
    border-radius: 3px;
}
.sourceName > p {
    font-size: 10px;
    font-weight: 500;
    padding-left: 10px;
}

.sourceName {
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
}

.blog > div > h1 {
    font-size: 25px;
    letter-spacing: -0.6px;
    line-height: 1.3em;
    padding: 10px 0;
    font-weight: 700;
    text-decoration: underline;
}

.blog > div > p {
    font-size: 13px;
    line-height: 1.6em;
    color: rgb(90, 90, 90);
    padding-bottom: 30px;
}

.no__blogs {
    font-size: 25px;
    font-weight: 500;
    line-height: 1.5em;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.home__page {
    width: 100%;
    height: 80vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.login__message {
    text-align: center;
    width:80%;
}

.login__message img{
     width:90px;
     margin-bottom: 15px;
}
.login__message h1 {
    font-size: 50px;
    text-decoration: underline;
    margin-bottom: 10px;
}
.login__message p {
    font-size: 20px;
    margin-bottom: 15px;
}
.login__button {
    padding: 10px 15px;
    background-color: black;
    color: white;
    outline: none;
    font-weight: 700;
    border-radius: 25px;
    border: black 2px solid;
}
.login__button:hover {
    background-color: #fff;
    color: gray;
    cursor: pointer;
}
.navbar {
    background: rgb(31, 31, 31);
    padding: 10px;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.navbar__header {
    padding: 7px;
}
.navbar__header img{
    width:30px;
}
.navbar__user__data {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
@media screen and (max-width: 720px) {
    .navbar__header {
      font-size: 20px;
    }
    .navbar__header img {
        width: 20px;
    }
  }


.signedIn {
    font-size: 18px;
    margin-left: 10px;
}

.logout__button {
    padding: 8px 16px;
    font-size: 15px;
    color: gray;
    border:none;
    border-radius: 10px;
    background: rgb(39, 39, 39);
    margin: 0px 8px;
    font-weight: 700;
}
.user {
    border: aquamarine 2px solid;
}

.logout__button img {
    margin: auto 1px;
    width: 16px;
}
.notSignedIn {
    font-size: 18px;
}

.blog__search {
    -webkit-flex:1 1;
            flex:1 1;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 10px px;
    margin: 0 10px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: rgb(39, 39, 39);
    border-radius: 5px;
}

.search {
    width: 80%;
    padding: 5px 10px;
    background: none;
    border:none;
    color:white;
    font-size:15px;
    outline:none;
}

.submit {
    width: 20%;
    color: white;
    font-size: 14px;
    border: none;
    cursor: pointer;
    margin-right: 10px;
    padding: 12px 0px;
    border-radius: 5px;
    transition: 0.1s ease-in-out;
    outline: none;
    background-color: rgb(39, 39, 39);
}
@media screen and (max-width: 670px) {
    .blog__search  {
      margin: 0;
    }
    .search {
        width: 60%;
    }
    .submit {
        width: 22%;
        font-size: 12px;
    }
    .logout__button {
        font-size: 13px;
    }
    .logout__button img {
        width: 12px;
    }
  }
  @media screen and (max-width: 480px) {
    .logout__button {
        display: none;
    }
    .navbar__header  img{
        display: none;
    }
  }
.submit:hover {
    background-color: rgb(39, 39, 39);
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Dancing Script', cursive;
}
